import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';

console.log(process.env.API_ROOT_DICT);
console.log(process.env.APP_ROUTER_BASE);
console.log(process.env.APP_TYPE);

var baseurl = process.env.API_ROOT_DICT;

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        appType: process.env.APP_TYPE, // 0-1-
        isLogin: false, // 
        isBLR: 0, // ,0-bind,1-login,2-register
        showLoginBox: false, // 
        mobileNbr: '',
        password: '',
        userCode: '',
        tokenCode: '',
        localCity: '',
        latitude: '',
        longitude: '',
        isChooseCity: false, // 
        zoneId: '2', // id
        from: '', // weixin
        weixin: {
            openId: '',
            code: '',
            htmldata: ''
        },
        global: {
            hftcom: baseurl,
            hftcomClient: baseurl + '/Api/Client',
            hftcomShop: baseurl + '/Api/Shop',
            hftcomWeixinCard: baseurl + '/Api/WeiXinCard',
            hftcomOpenID: baseurl + '/Admin/OpenId/getUserOpenId',
            hftcomGetUserInfo: baseurl + '/Api/Shop',
            Daipay: baseurl + '/Admin/Lcy/Daipay',
            Dipay: baseurl + '/Admin/Lcy/DiPay',
            getAT: baseurl + '/Admin/OpenId/getAccessToken'
        },
        userCouponList: [],
        indexCouponList: [], // 
        indexCouponDetailsInfo: {}, // 
        CouponListInfo: [], // 
        collectShopList: [], // 
        isbool: true, // 
        searchIsBool: true,
        searchShopList: [], // shop
        nearbyIsBool: true, // 
        nearbyShopList: [],  // shop
        searchConponIsBool: true, // 
        searchConponList: [], // shop
        searchTypeValue: 0, //   0  
        searchTypeName: '',  //     
        currentOrderInfo: [], // 
        isCancelOrder: false
    },
    getters,
    mutations,
    actions
});
export default store;



